@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600&display=swap);
:root {
  --orange: #ff7800;
  --black: #130f40;
  --light-color: #666;
  --box-shadow: 0 0.5rem 1.5rem rgba(0, 0, 0, 0.1);
  --border: 0.2rem solid rgba(0, 0, 0, 0.1);
  --outline: 0.1rem solid rgba(0, 0, 0, 0.1);
  --outline-hover: 0.2rem solid var(--black);
}
* {
  text-transform: capitalize;
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  text-decoration: none;
  border: none;
  transition: all linear 0.2s;
}
html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-padding-top: 7rem;
}
.btn {
  margin-top: 1rem;
  display: inline-block;
  padding: 0.8rem 3rem;
  font-size: 1.7rem;
  color: #130f40;
  color: var(--black);
  border: 0.2rem solid #130f40;
  border: 0.2rem solid var(--black);
  border-radius: 0.5rem;
  cursor: pointer;
  background: none;
}
.btn:hover {
  color: #fff;
  background: #ff7800;
  background: var(--orange);
}
section,
footer {
  padding: 2rem 9%;
}
body {
  background: #eee;
}
.heading {
  text-align: center;
  font-size: 3.5rem;
  color: #130f40;
  color: var(--black);
  padding: 2rem 0 3rem 0;
}
.heading span {
  background: #ff7800;
  background: var(--orange);
  color: #fff;
  display: inline-block;
  padding: 0.5rem 3rem;
  -webkit-clip-path: polygon(100% 0, 93% 50%, 100% 99%, 0 100%, 7% 50%, 0 0);
          clip-path: polygon(100% 0, 93% 50%, 100% 99%, 0 100%, 7% 50%, 0 0);
}

@media (max-width: 991px) {
  html {
    font-size: 55%;
  }

  .header {
    padding: 2rem!important;
  }

  section {
    padding: 2rem;
  }
}
@media (max-width:450px){

  html{
      font-size: 50%;
  }

  .heading{
      font-size: 2.5rem;
  }

  .footer{
      text-align: center;
  }

  .footer .box-container .box img{
      margin: 2rem auto;
  }

}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 9%;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 9999;
  background: #fff;
  box-shadow: var(--box-shadow);
}
.header .logo {
  font-size: 2.5rem;
  font-weight: bold;
  color: var(--black);
}
.header .logo i {
  color: var(--orange);
  margin-right: 0.5rem;
}
.header .icons button {
  height: 4.5rem;
  width: 4.5rem;
  line-height: 4.5rem;
  margin-left: 0.5rem;
  cursor: pointer;
  text-align: center;
  font-size: 2rem;
  background: #eee;
  border-radius: 0.5rem;
}
.header .icons button .fa-icon {
  color: var(--black);
  transition-duration: 0s;
}
.header .icons button:hover .fa-icon {
  color: #fff;
}
.header .icons button:hover {
  background: var(--orange);
}
.header .icons #menu-btn {
  display: none;
}

.header .navbar a {
  display: inline-block;
  color: var(--black);
  font-size: 1.7rem;
  margin: 0 2rem;
 
}
.header .navbar.active {
  right: 2rem;
  background-color: black;
}
.header .navbar a:hover {
  color: var(--orange);
  font-weight: bold;

 
  
  
}

@media (max-width: 768px) {
  .header {
    position: relative;
    
  }
  .header .icons #menu-btn {
    display: inline-block;
    
  }
  .header .navbar {
    position: absolute;
    top: 110%;
    right: -110%;
    width: 30rem;
    background: #fff;
    border-radius: 0.5rem;
  }
  .header .navbar a {
    display: block;
    font-size: 2rem;
    margin: 2rem 2.5rem;
    background-color: black;
   
  }
}
  a:is(:active).active{
    color: antiquewhite;
    background-color: rgb(0, 0, 0);
  }

.banner {
  padding-top: 17rem;
  padding-bottom: 10rem;
  display: flex;
  justify-content: center;
  font-size: 5rem;
  
  text-shadow: 10rem;
}
.banner .content {
  text-align: center;
  width: 110rem;
  height: 36rem;
  
}
.banner .content h3 {
  font-size: 3rem;
  color: var(--black);
}
.banner .content span {
  color: var(--orange);
}
.banner .content p {
  padding: 2rem 0;
  font-size: 1.7rem;
  color: var(--light-color);
}
.banner .content button:hover {
  background: var(--orange);
}


.features .heading {
  text-align: center;
  font-size: 3.5rem;
  color: var(--black);
  padding: 2rem 0 3rem 0;
}
.features span {
  background: var(--orange);
  color: #fff;
  display: inline-block;
  padding: 0.5rem 3rem;
  -webkit-clip-path: polygon(100% 0, 93% 50%, 100% 99%, 0 100%, 7% 50%, 0 0);
          clip-path: polygon(100% 0, 93% 50%, 100% 99%, 0 100%, 7% 50%, 0 0);
}
.features .content .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  grid-gap: 1.5rem;
  gap: 1.5rem;
  flex-wrap: wrap;
}
.features .content .box-container .box {
  text-align: center;
  box-shadow: var(--box-shadow);
  outline: var(--outline);
  outline-offset: -1rem;
  padding: 3rem 2rem;
  transition: linear 0.2s;
  background: #fff;
  font-size: 20px;
}
.features .content .box-container .box:hover {
  outline-offset: 0;
  outline: var(--outline-hover);
}
.features .content .box-container .box img {
  height: 15rem;
  margin: 1rem 0;
}
.features .content .box-container .box h3 {
  font-size: 2.5rem;
  line-height: 1.8;
  color: var(--black);
}
.features .content .box-container .box p {
  font-size: 1.8rem;
  line-height: 1.8;
  color:rgb(14, 20, 53);
  padding: 1rem 0;
}
.features .content .box-container .box a:hover {
  background: var(--orange);
}
